.contactUs {
  &__informations {
    background: $lightGrey;
    padding: $column-gutter-small rem-calc(35*2/3);
    margin-bottom: $column-gutter-small;
    text-align: center;

    &__heading {
      color: $jet;
      font-size: rem-calc(26*2/3);
      font-family: $font-bold;
      font-weight: $font-weight-normal;
      margin-bottom: rem-calc(6*2/3);
    }

    &__text {
      color: $blue;
      font-size: rem-calc(22*2/3);
      font-family: $font-bold;
      font-weight: $font-weight-normal;
      margin-bottom: rem-calc(20*2/3);
    }
  }

  .contactUs__cta,
  &__informations__cta {
    > a {
      display: block;
      width: 100%;
      margin-bottom: rem-calc(20*2/3);

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}


.contactUs-mini {
  .contactUs__cta {
    > * {
      display: block;
    }
  }
}


.contactUs-complete {
  .contactUs__informations {
    .btn-phone {
      width: 100%;
      display: block;
      margin-bottom: rem-calc(24*2/3);
    }
  }
  .contactUs__description {
    text-align: center;
    &__heading {
      color: $blue;
      font-size: rem-calc(24*2/3);
      font-family: $font-bold;
      font-weight: $font-weight-normal;
      margin-bottom: rem-calc(20*2/3);
    }
    &__text {
      font-size: rem-calc(22*2/3);
      margin-bottom: rem-calc(30*2/3);
    }
    a {
      width: 70%;
    }
  }
}

@media #{$medium-up} {


  .contactUs {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    @include display(flex);
    @include align-items(stretch);
    &.contactUs-complete {
      .wrapper {
        @include display(flex);
        @include align-items(left);
        @include flex-direction(row);
        @include justify-content(space-between);
        padding: rem-calc(30) rem-calc(35);
      }
      .contactUs__informations {
        position: relative;
        width: 50%;
        padding: 0 rem-calc(30) 0 0;
        .contactUs__informations__heading {
          font-size: rem-calc(24);
          margin-bottom: 0;
        }
        .contactUs__informations__text {
          font-size: rem-calc(24);
          margin-bottom: rem-calc(8);
        }
        .contactUs__informations__phone {
          font-size: rem-calc(24);
        }
        .contactUs__cta {
          padding-top: rem-calc(70);
          > a {
            float: none;
            width: auto;
            font-size: rem-calc(16);
            margin-bottom: rem-calc(13);
            border-radius: 5px;
          }
        }
      }
      .contactUs__description {
        position: relative;
        width: 50%;
        margin-bottom: 0;
        padding: 0 0 rem-calc(60) rem-calc(30);
        text-align: left;
        border-left: 1px solid $mediumGrey;
        .contactUs__description__heading {
          font-size: rem-calc(24);
        }
        .contactUs__description__text {
          font-size: rem-calc(16);
        }
      }
      .contactUs__location {
        position: absolute;
        bottom: 0;
        a {
          margin: 0;
          width: auto;
        }
      }
    }
    &__img,
    .wrapper {
      padding: 0;
      background: $lightGrey;
    }

    .wrapper {
      @include display(flex);
      @include align-items(center);
      @include flex-direction(column);
      @include justify-content(space-between);
    }

    &__img {
      width: 30%;
      background-size: cover;
    }

    &__informations {
      text-align: left;
      margin-bottom: 0;
      padding: rem-calc(30) rem-calc(35);
      max-width: 100%;

      &__heading {
        font-size: rem-calc(26);
        font-family: $font-bold;
        font-weight: $font-weight-normal;
        line-height: 1.2;
        margin-bottom: rem-calc(6*2/3);
      }

      &__text {
        font-size: rem-calc(18);
        font-family: $font-bold;
        font-weight: $font-weight-normal;
        margin-bottom: rem-calc(20*2/3);
        line-height: 1.2;
      }

      &__cta {
        > a {
          display: inline-block;
          vertical-align: top;
          width: auto;
          margin-right: rem-calc(20);
          border-radius: 5px;
        }
      }
    }

    .contactUs__cta {
      width: 100%;
      overflow: hidden;
      > a {
        float: left;
        width: 50%;
        border-radius: 0;
        font-size: rem-calc(26);
        margin-bottom: 0;
      }
    }
  }

  .contactUs-mini {
    .contactUs__cta {
      @include display(flex);
      @include align-items(stretch);
      @include justify-content(space-between);
      > * {
        display: block;
        width: 49%;
      }
    }
  }
}
@media #{$small-only} {
  .contactUs-mini .contactUs__cta {
    a:first-child {
      margin-bottom: rem-calc(16*2/3);
    }
  }
}
@media #{$large-up} {
  .contactUs {
    width: 71%;
    &__informations {
      &__text {
        font-size: rem-calc(20);
      }
    }
  }
}
