.programHeader {
  .slick-slider {
    margin-bottom: 0;
  }
  &__figure img {
    display: block;
    width: 100%;
  }
  .tag {
    line-height: 1;
    position: relative;
    top: rem-calc(-20*2/3);
  }

  .toolbox__intro,
  .content-price,
  .sharing, {
    padding: rem-calc(30*2/3) 0;
    margin-bottom: 0;
  }

  .toolbox {
    &__intro {
      font-family: $font-bold;
      font-weight: $font-weight-normal;
      text-align: center;
      border-bottom: 1px solid $mediumGrey;
      strong {
        font-family: $font-bold;
        font-weight: $font-weight-normal;
        color: $darkBlue;
      }
    }

    .contactUs {
      padding: rem-calc(30*2/3) 0;
    }

    .contactUs-mini {
      border-bottom: 1px solid $mediumGrey;
    }

    .save {
      @include display(flex);
      @include justify-content(center);
      @include align-items(center);
      height: rem-calc(40*2/3);
      margin: rem-calc(30*2/3) 0;
      .icon {
        font-size: rem-calc(60*2/3);
      }
      .text {
        color: $jet;
      }
    }
  }

  .content-price {
    text-align: center;
    &__item {
      .text {
        display: block;
      }
      .tags {
        > * {
          display: inline-block;
        }
      }
    }
  }

  .intro {
    font-family: $font-bold;
    font-weight: $font-weight-normal;
    color: #d7d7d7;
    line-height: 1.5;
    em {
      font-style: normal;
      color: $blue;
    }
    strong {
      color: #abb6ba;
    }
  }

  .tags {
    @include justify-content(center);
  }
}


// might be used on "bien" page
.content-price {
  list-style: none;
  margin: 0;

  &__item {
    margin-bottom: rem-calc(25*2/3);
    .text {
      color: $grey;
      font-family: $font-bold;
      font-weight: $font-weight-normal;
      font-size: rem-calc(36*2/3);
      margin-bottom: rem-calc(10*2/3);
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.characteristicList {
  list-style: none;
  margin: 0;
  @include display(flex);
  @include justify-content(space-between);
  @include flex-wrap(wrap);
  @include justify-content(flex-start);
  color: $grey;
  padding: rem-calc(20*2/3) 0;

  &__item {
    width: 49%;
    height: rem-calc(60);
    @include display(flex);
    @include align-items(center);

    .icon {
      font-size: rem-calc(40);
      margin-right: rem-calc(8);
    }
  }
}

.toolsList {
  @include grid-row();
  list-style: none;
  margin: 0 rem-calc(-10);
  padding: rem-calc(20*2/3) 0 0;
  li {
    @include grid-column(6);
  }
  a {
    width: 100%;
    @include display(flex);
    @include align-items(center);
    height: rem-calc(60);
    padding: 0 rem-calc(5);
    font-size: rem-calc(24*2/3);
    text-align: left;
    .icon {
      font-size: rem-calc(40);
    }
    .text {
      padding-left: rem-calc(10);
    }

    &.active {
      background: $mediumGrey;
    }
  }
}


@media #{$medium-up} {
  .programHeader {
    .slick-slider {
      margin: 0;
    }
    &__content {
      @include grid-row(
        $behavior: nest-collapse
      );
      position: relative;
      z-index: 2;
      margin-bottom: rem-calc(40);

      &:before {
        content: "";
        @include position(absolute, 0 0 0 0);
        background: $white;
        box-shadow: 0 0 0 rem-calc(40) $white;
        z-index: -1;
        display: block;
        @media #{$large-only} {
          box-shadow: 0 rem-calc(-20) 0 rem-calc(20) $white;
        }
      }
    }

    .save--small {
      display: none !important;
    }

    .toolbox {
      @include grid-column(4);
      padding-left: 0;
      &__intro {
        text-align: left;
        padding-top: 0;
      }
    }

    &__content__details {
      @include grid-column(
        $columns: 7,
        $offset: 1
      );
      padding-right: 0;
    }

    .content-price {
      text-align: left;
      &__item {
        .text {
          font-size: rem-calc(24);
        }
      }
    }

    .tags {
      @include justify-content(flex-start);
    }

    .sharing {
      border-bottom: none;

      &__items {
        @include justify-content(flex-start);
        margin-left: rem-calc(-10);
      }
    }

    .intro {
      padding: rem-calc(10) 22% 0 0;
      font-size: rem-calc(26);
    }
  }

  .characteristicList {
    &__item {
      width: 30%;
    }
  }

  .toolsList {
    margin: 0;
    li {
      @include grid-column(4);
    }
    a {
      height: rem-calc(50);
      padding: 0 rem-calc(20);
      font-size: rem-calc(14);
    }
  }
}

@media #{$medium-only} {
  .toolsList {
    li {
      @include grid-column(4);
    }
    a {
      height: rem-calc(50);
      padding: 0 rem-calc(5);
      font-size: rem-calc(11);
    }
  }
}

@media #{$small-only} {
  .programHeader {
    .sharing {
      border-top: 1px solid $mediumGrey;
      &__items__item {
        @include flex-grow(1);
        text-align: center;
        &:first-child {
          text-align: left;
        }
        &:last-child {
          text-align: right;
        }
      }
    }
    &__content__details  {
      display: table;
      .intro {
        display: table-header-group;
        position: relative;
        padding-top: rem-calc(80);
        &:after {
          content: "";
          display: block;
          position: relative;
          width: 100%;
          height: 1px;
          margin-top: rem-calc(20);
          background-color: $mediumGrey;
        }
      }
    }
  }
  .toolsList {
    li {
      padding: 0;
    }
    a {
      padding: 0;
      border: none;
      &:focus {
        background-color: transparent;
      }
    }
  }
}
