.moreInfoProgram {
  &__content {
    &__inner {
      background-color: $white;
      height: 100%;
      padding: rem-calc(20);

      .heading {
        padding-top: 0;
        margin-bottom: 0;
      }
    }
  }

  &__description {
    font-family: $font-bold;
    font-weight: $font-weight-normal;
  }

  .btn-wrapper {
    text-align: left;
    > * {
      margin-bottom: 0;
    }
  }
}

@media #{$medium-up} {
  .moreInfoProgram {
    @include grid-row(
      $behavior: nest
    );
    height: 100%;
    width: 100%;

    &__figure {
      @include grid-column(8);
      margin-bottom: 0;
      img {
        display: block;
      }
    }

    &__content {
      position: relative;
      @include grid-column(4);
      padding-right: 0;
      &__inner {
        @include display(flex);
        @include flex-direction(column);
        .heading {
          margin-top: rem-calc(20);
        }
      }
    }

    &__description {
      margin-top: 3%;
      margin-bottom: 3%;
      font-size: rem-calc(13);
    }

    .btn-wrapper {
      margin-top: auto;
    }
  }
}

@media #{$large-up} {
  .moreInfoProgram {
    &__description {
      font-size: rem-calc(16);
    }
  }
}

@media #{$small-only} {
  .moreInfoProgram {
    &__figure {
      margin-bottom: rem-calc(20*2/3);
    }
  }
}
