// TAG
.tag {
  display: inline-block;
  background-color: $blue;
  padding: rem-calc(13*2/3);
  color: $white;
  font-size: rem-calc(17*2/3);
  font-family: $font-bold;
  font-weight: $font-weight-normal;
  border-radius: 4px;
  box-shadow: inset 0 0 0 1px $blue;
  @include transition-property(all);
  @include transition-duration(.3s);
  @include transition-timing-function(ease-out);
  &.tag--important {
    background-color: $pink;
    box-shadow: inset 0 0 0 1px $pink;
  }

  @media #{$medium-up} {
    padding: rem-calc(8) rem-calc(10);
    font-size: rem-calc(13);
  }
}

button.tag,
a.tag {
  &:focus,
  &:hover,
  &:active {
    background-color: $white;
    color: $blue;
    box-shadow: inset 0 0 0 1px $blue;
  }

  .no-touch &:hover {
    background-color: $white;
    color: $blue;
    box-shadow: inset 0 0 0 1px $blue;
  }

  &.tag--important {
    &:focus,
    &:hover,
    &:active {
      background-color: $white;
      color: $pink;
      box-shadow: inset 0 0 0 1px $pink;
    }

    .no-touch &:hover {
      background-color: $white;
      color: $pink;
      box-shadow: inset 0 0 0 1px $pink;
    }
  }
}

.tva,
.tva--btn {
  display: inline-block;
  border-radius: 2px;
}

.tva {
  background-color: transparent;
  padding: rem-calc(7);
  color: $pink;
  border: 1px solid $pink;
  font-size: rem-calc(20*2/3);
  font-family: $font-bold;
  font-weight: $font-weight-normal;
  line-height: 1;
  text-align: center;

  &.tva--high {
    color: $blue;
    border: 1px solid $blue;
  }

  @media #{$medium-up} {
    font-size: rem-calc(10);
    width: rem-calc(70);
  }
}

.tva--btn {
  font-size: rem-calc(18*2/3);
  background: $lightGrey;
  color: $blue;
  padding: rem-calc(5) rem-calc(20*2/3) rem-calc(5) rem-calc(40*2/3);
  margin-left: rem-calc(10*2/3);
  position: relative;

  .icon {
    @include position(absolute, 50% null null rem-calc(10));
    @include transform(translateY(-50%));
    font-size: rem-calc(10);
  }
}

.tags {
  @include display(flex);
}