.squaredImageItem {
  margin-bottom: rem-calc(20);

  &.squaredImageItem--forcedRow {
    @include display(flex);
    @include flex-direction(row);
    // fix IE10 bugs
    overflow: visible;
    width:100%;
  }

  &__img {
    position: relative;
    display: block;
    width: 100%;

    img {
      max-width: 100%;
      width: 100%;
    }

    &__tags {
      @include position(absolute, rem-calc(10) rem-calc(10) null null);
      text-align: right;
      @extend .ul-unstyled;

      li {
        margin-bottom: rem-calc(10);
      }
    }
  }

  &__infos {
    position: relative;
    width: 100%;
    padding-left: 0;
    padding: rem-calc(20*2/3) 0;

    .heading {
      padding: 0;
      margin-bottom: 0;
      display: block;
      &__title {
        margin-bottom: 0;
        margin-right: rem-calc(10*2/3);
      }
    }

    @media #{$medium-up} {
      padding: rem-calc(20) 0;

      .heading__title {
        margin-right: rem-calc(10);
      }
    }
  }

  &__details {
    &__1,
    &__2 {
      margin: 0;
      line-height: rem-calc(30*2/3);
    }

    &__1 {
      margin-top: 7px;
      font-size: rem-calc(20*2/3);
      font-family: $font-bold;
      font-weight: $font-weight-normal;
      color: $jet;
    }
    &__2 {
      font-size: rem-calc(20*2/3);
      font-family: $font-regular;
      font-weight: $font-weight-normal;
      color: $grey;
      strong {
        font-family: $font-bold;
        font-weight: $font-weight-normal;
        font-size: rem-calc(30*2/3);
      }
    }

    .heading-favorite {
      @include display(flex);
      @include justify-content(space-between);
      > * {
        margin-bottom: 0;
      }
      .btn-icon--only {
        //margin-top: rem-calc(7);
      }
    }

    @media #{$medium-up} {
      &__1,
      &__2 {
        font-size: rem-calc(20);
        line-height: rem-calc(30);
      }
    }

    @media #{$large-up} {
      .heading-favorite {
        .btn-icon--only {
          margin-top: rem-calc(7);
        }
      }
    }
  }

  &__btn {
    margin-top: rem-calc(30);
    .btn-primary {
      margin-bottom: 0;
    }
  }
}


// When on grey background
.bg-lightGrey {
  .squaredImageItem {
    background: $white;

    &__date,
    &__infos {
      padding: rem-calc(20*2/3);
      margin-bottom: 0;
    }

    &__date {
      font-size: rem-calc(32*2/3);
      color: $blue;
      font-family: $font-bold;
      font-weight: $font-weight-normal;
    }

    @media #{$medium-up} {
      &__date,
      &__infos {
        padding: rem-calc(20);
      }

      .squaredImageItem__date{
        font-size: rem-calc(24);
      }
    }
  }
}


// inside a slick slider
.slick-slider__item {
  .squaredImageItem {
    margin-bottom: 0;
    height: 100%;
  }
}