.siteFooter {
  border-top: 1px solid $mediumGrey;
  padding-top: rem-calc(65*2/3);
  padding-bottom: rem-calc(50*2/3);

  &__head {
    margin-bottom: rem-calc(40*2/3);
    .logo {
      display: block;
      width: rem-calc(400*2/3);
      margin: 0 auto;
      margin-bottom: rem-calc(65*2/3);
      img {
        width: 100%;
      }
    }

    .prescripteurs {
      > * {
        display: block;
      }
    }
  }

  &__nav {
    ul {
      list-style: none;
      margin: 0;
      @include display(flex);
      @include flex-wrap(wrap);
      @include justify-content(center);

      li {
        margin-bottom: rem-calc(40*2/3);
      }

      a {
        display: block;
        padding: 0 rem-calc(20*2/3);
        font-family: $font-bold;
        font-weight: $font-weight-normal;
        color: $grey;
        font-size: rem-calc(22*2/3);
      }
    }
  }

  .aside {
    .sharing-label {
      display: none;
    }
  }
}


@media #{$medium-up} {
  .siteFooter {
    &__head {
      @include grid-row($behavior: nest);
      margin-bottom: rem-calc(30);

      .logo {
        @include grid-column(
          $columns: 3
        );
        margin-bottom: 0;
        margin-top: rem-calc(8);
        padding-left: 0;
      }
      .prescripteurs {
        @include grid-column(
          $columns: 4,
          $offset: 1
        );

        > * {
          display: inline-block;
          margin-bottom: 0;
        }
      }
      .aside {
        @include grid-column(
          $columns: 4,
          $float :right
        );
        margin-top: rem-calc(7);
      }
    }

    &__nav {
      @include grid-row($behavior: nest);

      ul {
        display: block !important;
        @include grid-column(
          $columns: 2
        );

        &:last-child {
          @include grid-column(
            $columns: 4
          );
        }

        a {
          padding: 0;
          font-size: rem-calc(14);
        }
      }

      .searchItems {
        @include grid-column(
          $columns: 2,
          $offset: 4
        );
      }
    }

    .aside {
      .sharing-label,
      .sharing {
        display: inline-block;
        vertical-align: middle;
      }
      .sharing-label {
        text-transform: uppercase;
        font-size: rem-calc(13);
        margin-right: rem-calc(10);
      }

      .sharing__items {
        font-size: rem-calc(35);
      }
    }

  }
}