.accordion {
  @extend .ul-unstyled;
  width: 100%;
  padding-left: 5%;
  padding-right: 5%;
  margin: 0 auto;

  &.fullWidth {
    padding: 0;
  }

  &__link {
    display: block;
    width: 100%;
    background: $lightGrey;
    padding: rem-calc(25*2/3);
    font-family: $font-bold;
    font-size: rem-calc(25*2/3);
    color: $jet;
    margin-bottom: rem-calc(10*2/3);
    position: relative;

    .display-status {
      position: absolute;
      right: rem-calc(25*2/3);
      border: 1px solid $mediumGrey;
      background: $white;
      width: rem-calc(45*2/3);
      height: rem-calc(45*2/3);

      &:before,
      &:after {
        content: "";
        background: $blue;
        @include position(absolute, 50% rem-calc(10*2/3) null rem-calc(10*2/3));
        margin-top: rem-calc(-1);
        height: rem-calc(2);
      }

      &:after {
        @include transform(rotate(90deg));
        @include transition-property(all);
        @include transition-duration(.3s);
        @include transition-timing-function(ease-out);
      }

      &--suppr {
        &:before {
          @include transform(rotate(45deg));
        }

        &:after {
          @include transform(rotate(-45deg));
        }
      }
    }

    &.active,
    [data-app-accordion-link].active {
      &.display-status:after,
      .display-status:after {
        @include transform(rotate(0));
      }
    }

    &:focus,
    &:hover {
      color: $jet;
    }
  }

  @media #{$medium-up} {
    padding-left: 0;
    padding-right: 0;
  }
}

.wrapper--narrow {
  .accordion {
    &__link {
      padding: rem-calc(20*2/3);
    }
  }
}

.bg-lightGrey,
.bg-mediumGrey {
  .accordion__link {
    background: $white;
  }
}