.sticky {
  .fixed {
    box-shadow: 0 0 rem-calc(2) 0 rgba($black, .5);
    background: $white;
  }
}

.header {
  overflow: visible;
  height: $topbar-height;
  line-height: $topbar-height;
  position: relative;

  &__menu {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    width: auto;

    .main-menu {
      @include display(flex);
      @include align-items(center);
      height: auto !important;
      width: auto;
      vertical-align: middle;
      margin: 0;

      &__item {
        list-style: none;
        padding: 0 0 0 rem-calc(40);

        a:not(.btn-primary) {
          color: $topbar-link-color;
          line-height: $topbar-height;
          font-size: $topbar-link-font-size;
          font-weight: $topbar-link-weight;
          display: block;
          text-transform: none;
          width: 100%;
          margin-bottom: 0;

          &:hover {
            color: $topbar-link-color-hover;
          }
        }

        a.btn-primary {
          margin-bottom: 0;
          padding: rem-calc(14) rem-calc(15);
          .button__content {
            padding-left: rem-calc(25);
          }
        }
      }
    }
  }

  &__search {
    background: $white;
    height: $topbar-height;
    opacity: 0;
    @include transition-property(all);
    @include transition-duration(.5s);
    @include transition-timing-function(ease-out);
    z-index: 1;

    &.opened {
      opacity: 1;
    }

    .smallSearch {
      @include display(flex);
      @include align-items(center);

      &__input {
        position: relative;
        input {
          margin-bottom: 0;
        }
      }

      .topbarSearchError {
        display: none;
        line-height: 1;
        @include position(absolute, null 0 0 0);
        padding: rem-calc(3) rem-calc(18);
        color: $alert-color;
        font-size: rem-calc(11);
      }
    }

    form.error {
      .topbarSearchError {
        display: block;
      }
    }
  }
}


.title-area {
  margin: 0;
  list-style: none;
  @include display(flex);

  .name {
    display: table;
    width: rem-calc(300*2/3);
    margin: auto;
    height: 100%;
    padding-right: rem-calc(15);
  }

  &__link {
    display: table-cell;
    vertical-align: middle;
    line-height: 1;
    img {
      display: block;
      width: 100%;
    }
  }

  &__search {
    @include display(flex);
  }
}


// burger button
.menu-btn {
  display: block;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: rem-calc(30*2/3);
  height: $topbar-height;
  font-size: 0;
  text-indent: -9999px;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: none;
  cursor: pointer;
  background: $white;

  @include transition-property(background);
  @include transition-duration(.2s);
  @include transition-timing-function(ease-out);

  &:focus {
    outline: none;
    box-shadow: none;
  }

  span {
    display: block;
    @include position(absolute, 50% 0 null 0);
    @include transform(translateY(-50%));
    height: rem-calc(2);
    background: $jet;

    @include transition-property(background);
    @include transition-duration(.2s);

    &:before,
    &:after {
      position: absolute;
      display: block;
      left: 0;
      width: 100%;
      height: rem-calc(2);
      background-color: $jet;
      content: "";

      @include transition-duration(0.2s, 0.2s);
      @include transition-delay(0.2s, 0s);
    }

    &:before {
      top: -6px;
      @include transition-property(top, transform);
    }

    &:after {
      bottom: -6px;
      @include transition-property(bottom, transform);
    }
  }

  &.is-active span {
    background-color: $white;

    &:before {
      top: 0;
      transform: rotate(45deg);
    }

    &:after {
      bottom: 0;
      transform: rotate(-45deg);
    }

    &:before,
    &:after {
      transition-delay: 0s, 0.3s;
    }
  }
}

.smallSearch__close {
  display: block;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: rem-calc(30*2/3);
  height: $topbar-height;
  background: $white;
  font-size: 0;
  text-indent: -9999px;

  &:focus {
    outline: none;
    box-shadow: none;
  }

  span {
    display: block;
    @include position(absolute, 50% 0 null 0);
    @include transform(translateY(-50%));
    height: rem-calc(2);
    background: white;

    &:before,
    &:after {
      position: absolute;
      display: block;
      left: 0;
      width: 100%;
      height: rem-calc(2);
      background-color: $jet;
      content: "";
    }

    &:before {
      top: 0;
      transform: rotate(45deg);
    }

    &:after {
      bottom: 0;
      transform: rotate(-45deg);
    }
  }
}


@media #{$small-only} {
  .header {
    &__menu {
      display: none;
    }

    &__search {
      @include position(absolute, 0 0 0 0);
      @include transform(translateX(100%));

      &.opened {
        @include transform(translateX(0));
      }

      .smallSearch {
        @include justify-content(space-between);
        &__input {
          width: 70%;
        }
      }
    }
  }

  .title-area {
    @include position(absolute, 0 $topbar-height 0 $topbar-height);

    &__search {
      @include position(fixed, rem-calc(35*2/3) rem-calc(25) null null);
      &.notVisible {
        opacity: 1;
      }
      > * {
        font-size: rem-calc(18);
      }
    }
  }
}

@media #{$medium-only} {
  .header {
    &__menu {
      .main-menu {
        &__item {
          padding: 0 0 0 rem-calc(20);
        }
      }
    }
  }
}


@media #{$medium-up} {
  .header {
    margin: 0 auto;
    margin-bottom: 0;
    @include display(flex);
    @include justify-content(space-between);
    @include align-items(center);

    &__search {
      @include position(absolute, 0 0 0 rem-calc(340));
      border-left: 1px solid $mediumGrey;
      @include transform(translateX(100%));
      max-width: none;
      width: auto;

      &.opened {
        @include transform(translateX(0));
      }

      .smallSearch {
        @include justify-content(space-between);

        &__input {
          width: 70%;
          margin-left: rem-calc(20);
          border: none;
        }

        .topbarSearchError {
          padding: rem-calc(3) rem-calc(22);
          font-size: rem-calc(11);
        }

        &__close {
          margin-left: auto;
        }
      }
    }
  }

  .title-area {
    .name {
      width: rem-calc(300);
    }

    &__link {
      img {
        position: relative;
        top: rem-calc(-3);
      }
    }

    &__search {
      @include transition-property(all);
      @include transition-duration(.2s);
      @include transition-timing-function(ease-out);
      @include position(absolute, 0 null 0 rem-calc(340));
      border-left: 1px solid $mediumGrey;
      padding-left: rem-calc(10);

      &.notVisible {
        opacity: 0;
      }
    }
  }
}
