.bienHeader {
  .toolbox__intro {
    border-bottom: none;
    text-align: left;
    &--border {
      padding: rem-calc(20) 0;
      margin-top: rem-calc(20);
      border: 1px solid $mediumGrey;
      border-left: none;
      border-right: none;
    }
  }
  .content-price {
    &.bienPrice {
      text-align: left;
      padding-bottom: 0;
      .content-price__item {
        @include display(flex);
        @include justify-content(flex-start);
        @include align-items(center);
      }
      .text {
        font-size: rem-calc(15);
        margin: 0;
        padding-right: rem-calc(10);
      }
      .tags {
        font-size: rem-calc(10);
      }
      .tva{
        font-size: rem-calc(10);
        &--btn {
          margin-left: rem-calc(10);
          font-size: rem-calc(10);
          white-space: nowrap;
        }
      }
    }
  }
  .toolsList {
    padding: rem-calc(20) 0 0;
    border: 1px solid $mediumGrey;
    border-left: none;
    border-right: none;
  }
  .phone-green {
    margin: rem-calc(35*2/3) 0;
  }
}

@media #{$medium-up} {
  .bienHeader {
    .slick-slider .slick-prev,
    .slick-slider .slick-next {
      @include transform(translateY(-100%));
    }
    .heading {
      margin-bottom: rem-calc(10);
    }
    .toolbox__intro {
      padding: 0;
      &--border {
        padding: rem-calc(20) 0;
      }
    }
    .programHeader__content {
      top: rem-calc(-20);

      &:before {
        content: "";
        @include position(absolute, 0 0 0 0);
        background: $lightGrey;
        box-shadow: 0 0 0 rem-calc(20) $lightGrey;
        z-index: -1;
        display: block;
      }

      .toolbox,
      &__details {
        position: relative;
        padding: rem-calc(20);
        padding-top: rem-calc(30);
        background-color: $white;
      }

      .toolbox {
        width: 36%;
        padding-bottom: 0;
      }

      &__details {
        width: 62%;
        float: right;
        height: 100%;
        margin-left: 0 !important;
      }
    }
    .contactUs-mini {
      position: absolute;
      left: rem-calc(20);
      right: rem-calc(20);
      bottom: 0;
      > a {
        text-align: center;
      }
    }
    .content-price {
      &.bienPrice {
        .text {
          font-size: rem-calc(20);
        }
        .tva{
          font-size: rem-calc(12);
          &--btn {
            font-size: rem-calc(12);
          }
        }
      }
    }
  }
}

@media #{$medium-only} {
  .bienHeader {
    margin-bottom: rem-calc(10);
    .heading__title {
      font-size: rem-calc(24);
    }
    .content-price {
      &.bienPrice {
        .text {
          font-size: rem-calc(17);
        }
        .tva{
          font-size: rem-calc(10);
          &--btn {
            font-size: rem-calc(10);
          }
        }
      }
    }
    .programHeader__content {
      width: 96%;
      margin: 0 auto;

      .toolbox {
        width: 37%;
      }

      &__details {
        width: 61%;
      }
    }
    .contactUs-mini > a {
      margin-bottom: rem-calc(15);
    }
  }
}
@media #{$small-only} {
  .bienHeader {
    .toolbox__intro {
      border-bottom: 1px solid $mediumGrey;
    }
    .sharing {
      width: 100%;
    }
    .toolsList li {
      padding-right: rem-calc(5);
      a {
        display: block;
        height: auto;
        min-height: 40px;
        padding-left: rem-calc(45);
        .icon {
          @include position(absolute, 50% null null 0);
          margin-top: -22px;
        }
        .text {
          padding-left: 0;
        }
      }
    }
  }
}
