.homepageB2B {
  position: relative;

  &__heading {
    padding: rem-calc(95*2/3) 0 rem-calc(138*2/3);
    background-size: cover;
    background-position: top center;

    .heading {
      margin-bottom: rem-calc(18*2/3);

      &__title {
        text-transform: uppercase;
      }
      &__title--sub {
        text-transform: none;
        font-size: rem-calc(24*2/3);
      }
    }

    .btn-wrapper.btn-wrapper--center {
      text-align: left;
    }
  }
  &__login {
    padding: rem-calc(50*2/3) 0;
    position: relative;
    .btn-primary {
      width: 100%;
    }
    .heading--switchColor {
      padding-top: rem-calc(15);
      margin-bottom: rem-calc(15);
      .heading__title {
        font-size: rem-calc(36*2/3);
      }
    }
    &--textfield {
      .form-text {
        @include placeholder {
          color: $black;
          font-weight: $font-weight-medium;
        }
      }
    }
    &--inlineGroup {
      @include display(flex);
      @include justify-content(space-between);
      padding-bottom: rem-calc(20*2/3);
      .textLink {
        font-size: rem-calc(20*2/3);
        color: $grey;
        line-height: 1;
      }
      .homepageB2B__login--checkbox {
        .label-checkbox {
          padding: 0;
          &:before {
            width: rem-calc(30*2/3);
            height: rem-calc(30*2/3);
            min-width: rem-calc(30*2/3);
            min-height: rem-calc(30*2/3);
            margin-top: rem-calc(-3);
          }
        }
        span {
          font-size: rem-calc(20*2/3);
          color: $grey;
        }
      }
    }
  }
}

@media #{$medium-up} {
  .homepageB2B {
    &__heading {
      padding: rem-calc(90) 0;
      .heading {
        z-index: 1;
        @media #{$medium-only} {
          max-width: rem-calc(390);
        }
        &__title {
          font-size: rem-calc(50);
          @media #{$medium-only} {
            font-size: rem-calc(35);
          }
        }
        &__title--sub {
          font-size: rem-calc(30);
          @media #{$medium-only} {
            font-size: rem-calc(25);
          }
        }
      }
    }
    .homepageB2B__popin__btn {
      position: relative;
      z-index: 1;
      font-size: rem-calc(19);
    }
    &__login {
      width: 100%;
      @include position(absolute, 50% null null null);
      @include transform(translateY(-50%));
      .btn-primary {
        width: 50%;
      }
      &--inlineGroup {
        @include flex-direction(row-reverse);
      }
      &--inner {
        padding: rem-calc(20 45);
        width: rem-calc(410);
        float: right;
        background-color: rgba(0, 0, 0, .5);
        .heading--switchColor {
          .heading__title {
            font-size: rem-calc(20);
            color: $white;
          }
        }
        .textLink, p, .label-checkbox span {
          color: $white;
          opacity: .7;
        }
      }
    }
  }
}
