.results {
  &__list {
    &__heading {
      .heading__title--sub {
        font-size: rem-calc(30*2/3);
      }
    }
  }

  .tabs-content,
  &__items {
    border-top: 1px solid $mediumGrey;
    clear: both;
    ul {
      list-style: none;
      margin-left: 0;
    }
  }

  &__item {
    border-bottom: rem-calc(10) solid $lightGrey;
    &:last-child {
      border-bottom: none;
    }
  }

  .filter {
    padding-bottom: rem-calc(35*2/3);
    &__item {
      margin-bottom: 0;
    }
  }


  // BIENS
  .results-accordion {
    padding-top: rem-calc(40*2/3);

    > li {
      margin-bottom: rem-calc(20*2/3);

      a:hover {
        background: $lightGrey;
      }
    }

    .media__content {
      .text {
        color: $jet;
        font-size: rem-calc(20*2/3);
        font-family: $font-bold;
        font-weight: $font-weight-normal;
        margin-bottom: 0;
      }
    }
  }

  .media {
    padding: rem-calc(20*2/3);
    background: $lightGrey;
    @include align-items(center);

    &__img {
      width: 30%;
    }
    .heading {
      padding: 0;
      &__title {
        font-size: rem-calc(28*2/3);
      }
    }
  }
}


.searchResultsItem {
  padding-bottom: rem-calc(20);

  .heading {
    padding-bottom: 0;

    .tag {
      margin-top: rem-calc(20);
    }
  }
  .heading__title,
  .heading__title--sub {
    font-size: rem-calc(30*2/3);
  }
  .heading__title {
    display: inline-block;
  }
  .heading__title--sub {
    margin-bottom: 0;
    &:before {
      content: "| ";
      margin: 0 rem-calc(8*2/3);
      display: inline-block;
      vertical-align: text-bottom;
    }
  }

  &__infos {
    margin-top: rem-calc(35*2/3);
    @include display(flex);
    @include justify-content(space-between);

    &__img {
      padding-right: rem-calc(30*2/3);
      width: 40%;
    }

    &__details {
      width: 60%;
      .intro {
        color: $jet;
        font-family: $font-bold;
        font-weight: $font-weight-normal;
        font-size: rem-calc(24*2/3);
        margin-bottom: rem-calc(15*2/3);
      }

      .details {
        @include display(flex);
        font-size: rem-calc(20*2/3);
        border-bottom: 1px solid $mediumGrey;
        margin-bottom: rem-calc(25*2/3);
        padding-bottom: rem-calc(25*2/3);

        li {
          margin-right: rem-calc(30*2/3);
        }
      }

      .prices {
        margin-bottom: rem-calc(25*2/3);
        .text {
          color: $grey;
          margin-right: rem-calc(10*2/3);
          display: inline-block;
          vertical-align: middle;
        }
      }
    }

    .searchResultsItem__btn {
      > * {
        margin-bottom: 0;
      }
    }
  }


  // BIENS
  &--biens {
    .searchResultsItem__infos__img {
      .heading {
        padding: 0;
        margin-top: 0;
        .heading__title,
        .heading__title--sub {
          font-size: rem-calc(24*2/3);
        }
        .heading__title {
          font-size: rem-calc(24*2/3);
        }
        .heading__title--sub:before {
          display: none;
        }
      }
    }

    .searchResultsItem__infos__details {
      .intro {
        font-size: rem-calc(20*2/3);
      }
    }
  }
}


@media #{$medium-up} {
  .results {
    @include grid-row(
      $behavior: collapse
    );

    .results__map {
      @include grid-column(
        $columns: 5,
        $collapse: true
      );
      min-height: rem-calc(100);

      .gmaps {
        min-height: rem-calc(100);
        width: 39.5%;
        position: absolute;
        left: 0;
      }
    }

    .results__list {
      @include grid-column(
        $columns: 7,
        $collapse: true
      );
    }

    &__list__heading {
      float: left;
    }
    .filter {
      float: right;
      margin-top: rem-calc(60);
      &__label {
        margin-right: rem-calc(20);
      }
    }

    .media {
      padding: rem-calc(20);
      &__img {
        width: rem-calc(130);
      }
      .heading {
        &__title {
          font-size: rem-calc(28);
        }
      }
    }


    // BIENS
    .results-accordion {
      > li {
        margin-bottom: rem-calc(20);
      }
      .media__content {
        .text {
          font-size: rem-calc(16);
        }
      }

      &__content {
        padding: 0;
        padding-left: rem-calc(20);
      }
    }
  }


  .searchResultsItem {
    padding-bottom: rem-calc(30);
    padding-top: 0;

    .heading {
      padding-top: 0;
      margin-bottom: rem-calc(20);
      display: table;
      width: 100%;

      .heading__title,
      .heading__title--sub {
        font-size: rem-calc(22);
      }

      > * {
        display: table-cell;
        padding-top: rem-calc(20);
      }

      .promotion {
        padding-top: 0;
        text-align: right;
      }
    }

    &__infos {
      @include grid-row();
      &__img {
        @include grid-column(4);
        padding-left: 0 !important;
      }
      &__details {
        @include grid-column(8);
        padding-right: 0 !important;
        padding-left: rem-calc(30);
        margin-top: rem-calc(20);
      }
    }

    .details {
      font-size: rem-calc(16);
      li {
        margin-right: rem-calc(35);
      }
    }

    &__infos__details {
      .prices {
        li {
          @include display(flex);
          @include justify-content(space-between);
          @include align-items(center);
        }
        .text {
          font-size: rem-calc(20);
        }
      }
    }


    // BIENS
    &--biens {
      .heading {
        display: block;
        > * {
          display: block;
          padding-top: 0;
        }

        .heading__title,
        .heading__title--sub {
          font-size: rem-calc(22);
        }
        .promotion {
          text-align: left;
        }
      }

      margin-top: rem-calc(20);
      .searchResultsItem__infos__img {
        .heading {
          .heading__title {
            font-size: rem-calc(22);
          }
        }
      }
      .searchResultsItem__infos__details {
        .intro {
          font-size: rem-calc(16);
        }
      }

      .searchResultsItem__infos {
        &__img {
          @include grid-column(5);
        }
        &__details {
          @include grid-column(7);
          margin-top: 0;
        }
      }
    }

  }
}


@media #{$medium-only} {
  .results {
    .filter {
      @include flex-direction(column);
      margin: rem-calc(40) 0 0 0;

      &__label {
        @include align-self(flex-end);
        margin: 0 0 rem-calc(10) 0;
        padding-right: 0;
      }
    }

    &__list__heading {
      .heading__title--sub {
        font-size: rem-calc(20);
        margin-top: rem-calc(10);
      }
    }
  }
}


@media #{$large-up} {
  .results {
    .results__map {
      @include grid-column(
        $columns: 4,
        $collapse:true
      );
      .gmaps {
        min-height: rem-calc(100);
        width: 33%;
      }
    }
    .results__list {
      @include grid-column(
        $columns: 8,
        $collapse: true
      );
    }

    .large-column {
      @include display(flex);
      @include align-items(flex-end);
      @include justify-content(space-between);
    }

    .searchResultsItem__btn {
      text-align: right;
    }
  }

  .searchResultsItem {
    &__infos {
      &__img {
        @include grid-column(3);
      }
      &__details {
        @include grid-column(9);

        .prices {
          margin-bottom: 0;
          .text {
            margin-right: rem-calc(20);
          }
        }
      }
    }

    // searchResultsItem : BIENS
    &--biens {
      .searchResultsItem__infos {
        &__img {
          @include grid-column(4);
        }
        &__details {
          @include grid-column(8);
        }
      }
    }
  }

}
