.moreAvailable {
  table {
    width: 100%;
    border-spacing: 0;
    border-collapse: collapse;
    border: none;
  }

  td {
    padding: rem-calc(15) rem-calc(5);
    border: none;
    vertical-align: middle;
    &:first-child {
      font-family: $font-bold;
      font-weight: $font-weight-normal;
    }
    &:last-child {
      text-align: right;
      @include display(flex);
      @include align-items(center);
      @include justify-content(space-between);
      .btn-rounded {
        margin-bottom: 0;
        margin-left: rem-calc(20);
        padding-top: rem-calc(6);
        padding-bottom: rem-calc(6);
      }
    }
  }

  .list-item,
  .list-price,
  .list-characteristics ul,
  .item-ulities ul {
    list-style: none;
    margin: 0;
  }

  .item-ulities ul {
    li {
      white-space: nowrap;
      display: inline-block;
    }
  }

  .item-ulities {
    @include flex(3);
  }

  .item-area,
  .item-exhibit {
    white-space: nowrap;
    @include flex(1);
  }

  .list-characteristics {
    > ul {
      @include display(flex);
      width: 100%;
      margin: 0;
      > * {
        display: table-cell;
        vertical-align: middle;
        padding: 0 rem-calc(20*2/3) 0 0;
      }
    }
  }

  .list-item {
    display: table;
    width: 100%;
    > * {
      display: table-cell;
      vertical-align: middle;
    }
  }

  .list-price {
    li:last-child {
      margin-top: rem-calc(12*2/3);
    }
    .text,
    .tva {
      vertical-align: middle;
    }
    .text {
      margin-right: rem-calc(8*2/3);
      font-family: $font-bold;
      font-weight: $font-weight-normal;
    }
  }
}

@media #{$medium-up} {
  .moreAvailable {
    table {
      border: rem-calc(20*2/3) solid $lightBlue;
    }

    tr {
      &:nth-of-type(odd) {
        background-color: $lightGrey;
      }
      &:nth-of-type(even) {
        background-color: $white;
      }
    }
    td {
      vertical-align: middle;
      padding-left: rem-calc(20);
      padding-right: rem-calc(20);
    }
    td:first-child {
      font-size: rem-calc(18);
      width: rem-calc(70);
    }

    .item {
      &-promotion {
        width: 25%;
        padding-right: rem-calc(20);
      }
    }

    .list-characteristics {
      .item-ulities ul {
        li {
          &:before {
            content: "- ";
          }
          &:first-child:before {
            content: "";
          }
        }
      }
    }

    .list-price {
      li {
        white-space: nowrap;
      }
      .text {
        margin-right: rem-calc(20*2/3);
      }
    }
  }
}

@media #{$small-only} {
  .moreAvailable {
    table {
      tr {
        display: block;
        border: 2px solid $lightGrey;
        background: $lightGrey;
        margin-bottom: rem-calc(20*2/3);
      }

      td {
        display: inline-block;
        font-size: rem-calc(13);
        width: 83%;

        &:first-child {
          padding-left: rem-calc(10);
          width: 15%;
        }
        &:last-child {
          @include flex-wrap(wrap);
          @include justify-content(space-around);
          width: 100%;
          background: $white;
          padding-right: rem-calc(10);
        }
      }
    }

    .tag--important {
      padding: rem-calc(5);
      position: relative;
    }
    .list-item {
      li {
        font-size: rem-calc(13);
      }
    }

    .item-promotion {
      display: table-caption;
    }

    .item-utilities {
      padding-right: rem-calc(10);
    }

    .list-price {
      display: table;
      margin: rem-calc(5) 0;
      li {
        display: table-cell;
        vertical-align: middle;
        padding-right: rem-calc(10);
        text-align: center;
      }
      li:last-child {
        margin-top: rem-calc(12*2/3);
        padding-right: 0;
      }
      .text {
        font-size: rem-calc(13);
        margin-right: rem-calc(10*2/3);
      }
      .tva {
        font-size: rem-calc(9);
      }
    }
  }
}
