.sharing {
  &__items {
    list-style: none;
    margin: 0;
    @include display(flex);
    @include justify-content(center);
    @include align-items(center);
    font-size: rem-calc(50);
    line-height: 1;

    .icon {
      display: block;
      color: $jet;

      .no-touch &:hover {
        color: $blue;
      }
    }
  }

  @media #{$medium-up} {
    &__items {
      font-size: rem-calc(40);
    }
  }
}